body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #2a2a2a;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.container {
  text-align: center;
  background: #333;
  border: 2px solid #444;
  padding: 35px;
  border-radius: 12px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.4);
  max-width: 500px;
  width: 100%;
}

.title {
  margin-bottom: 25px;
  font-size: 32px;
  color: #e1e1e1;
  font-weight: bold;
  position: relative;
  display: inline-block;
}

.title::before {
  content: "";
  position: absolute;
  left: -35px;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  background-image: url("https://cdn-icons-png.flaticon.com/512/3310/3310608.png");
  background-size: cover;
  background-position: center;
}

.title::after {
  content: "";
  position: absolute;
  right: -35px;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  background-image: url("https://cdn-icons-png.flaticon.com/512/3310/3310608.png");
  background-size: cover;
  background-position: center;
}

.title:hover {
  color: #28a745;
  text-shadow: 2px 2px 8px rgba(40, 167, 69, 0.7);
}

.title:hover::before,
.title:hover::after {
  transform: translateY(-50%) scale(1.1);
}

.converter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
}

.input,
.dropdown,
.result {
  padding: 8px;
  font-size: 14px;
  border: 1px solid #555;
  border-radius: 6px;
  transition: border 0.3s, box-shadow 0.3s;
  background-color: #444;
  color: #fff;
}

.input {
  margin-right: 15px;
}

.dropdown {
  margin-left: 2px;
  width: auto;
  max-width: 120px;
}

.input:focus,
.input:hover,
.dropdown:focus,
.dropdown:hover,
.result:focus,
.result:hover {
  border-color: #28a745;
  box-shadow: 0 0 8px rgba(40, 167, 69, 0.5);
}

.swap-icon {
  cursor: pointer;
  padding: 14px;
  background-color: #555;
  border-radius: 50%;
  margin: 0 10px;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out,
    background-color 0.3s ease;
}

.swap-icon:hover {
  transform: scale(1.2) rotate(180deg);
  box-shadow: 0 0 15px rgba(40, 167, 69, 0.7);
  background-color: #28a745;
}

.swap-icon svg {
  width: 30px;
  height: 30px;
  transition: fill 0.3s ease;
}

.swap-icon:hover svg {
  fill: #fff;
}

.result {
  margin-top: 25px;
  font-weight: bold;
  color: #ffffff;
  text-align: center;
  width: 100%;
  padding: 12px;
  box-sizing: border-box;
  word-wrap: break-word;
  background-color: #444;
}

.footer {
  margin-top: 30px;
  font-size: 14px;
  color: #e1e1e1;
  text-align: center;
  padding: 15px;
  background-color: #333;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.footer p {
  margin: 5px 0;
}

.footer a {
  color: #28a745;
  text-decoration: none;
  font-weight: bold;
}

.footer a:hover {
  text-decoration: underline;
}
